  export default "import { Actions } from '@ant-design/x';\nimport { Divider } from 'antd';\nimport React from 'react';\nimport SemanticPreview from '../../../.dumi/components/SemanticPreview';\nimport useLocale from '../../../.dumi/hooks/useLocale';\n\nconst locales = {\n  cn: {\n    root: '根节点',\n    default: '默认图标',\n    loading: '加载图标',\n    running: '运行图标',\n    error: '错误图标',\n  },\n  en: {\n    root: 'Root',\n    default: 'Default',\n    loading: 'Loading',\n    running: 'Running',\n    error: 'Error',\n  },\n};\n\nconst App: React.FC = () => {\n  const [locale] = useLocale(locales);\n  return (\n    <>\n      <SemanticPreview\n        componentName=\"Actions.Audio\"\n        semantics={[\n          { name: 'root', desc: locale.root },\n          { name: 'default', desc: locale.default },\n        ]}\n      >\n        <Actions.Audio />\n      </SemanticPreview>\n      <Divider style={{ margin: 0, padding: 0 }} />\n      <SemanticPreview\n        componentName=\"Actions.Audio\"\n        semantics={[{ name: 'loading', desc: locale.loading }]}\n      >\n        <Actions.Audio status=\"loading\" />\n      </SemanticPreview>\n      <Divider style={{ margin: 0, padding: 0 }} />\n      <SemanticPreview\n        componentName=\"Actions.Audio\"\n        semantics={[{ name: 'running', desc: locale.running }]}\n      >\n        <Actions.Audio status=\"running\" />\n      </SemanticPreview>\n      <Divider style={{ margin: 0, padding: 0 }} />\n      <SemanticPreview\n        componentName=\"Actions.Audio\"\n        semantics={[{ name: 'error', desc: locale.error }]}\n      >\n        <Actions.Audio status=\"error\" />\n      </SemanticPreview>\n    </>\n  );\n};\n\nexport default App;\n";;
  