  export default "import { Actions } from '@ant-design/x';\nimport { Divider } from 'antd';\nimport React from 'react';\nimport SemanticPreview from '../../../.dumi/components/SemanticPreview';\nimport useLocale from '../../../.dumi/hooks/useLocale';\n\nconst locales = {\n  cn: {\n    root: '根节点',\n    like: '喜欢',\n    liked: '已喜欢',\n    dislike: '不喜欢',\n    disliked: '已不喜欢',\n  },\n  en: {\n    root: 'Root',\n    like: 'Like',\n    liked: 'Liked',\n    dislike: 'Dislike',\n    disliked: 'Disliked',\n  },\n};\n\nconst App: React.FC = () => {\n  const [locale] = useLocale(locales);\n  return (\n    <>\n      <SemanticPreview\n        componentName=\"Actions.Feedback\"\n        semantics={[\n          { name: 'root', desc: locale.root },\n          { name: 'like', desc: locale.like },\n          { name: 'dislike', desc: locale.dislike },\n        ]}\n      >\n        <Actions.Feedback />\n      </SemanticPreview>\n      <Divider style={{ margin: 0, padding: 0 }} />\n      <SemanticPreview\n        componentName=\"Actions.Feedback\"\n        semantics={[{ name: 'liked', desc: locale.liked }]}\n      >\n        <Actions.Feedback value=\"like\" />\n      </SemanticPreview>\n      <Divider style={{ margin: 0, padding: 0 }} />\n      <SemanticPreview\n        componentName=\"Actions.Feedback\"\n        semantics={[{ name: 'dislike', desc: locale.dislike }]}\n      >\n        <Actions.Feedback value=\"dislike\" />\n      </SemanticPreview>\n    </>\n  );\n};\n\nexport default App;\n";;
  