  export default "import { CodeHighlighter } from '@ant-design/x';\nimport { Button, Space } from 'antd';\nimport React from 'react';\n\nconst App = () => {\n  const code = `import { useState } from 'react';\n\nfunction Counter() {\n  const [count, setCount] = useState(0);\n  return (\n    <div>\n      <p>当前计数：{count}</p>\n      <button onClick={() => setCount(count + 1)}>增加</button>\n    </div>\n  );\n}`;\n\n  const customHeader = (\n    <div\n      style={{\n        display: 'flex',\n        justifyContent: 'space-between',\n        alignItems: 'center',\n        padding: '8px 16px',\n        background: '#f5f5f5',\n      }}\n    >\n      <Space>\n        <span style={{ fontWeight: 500 }}>React 计数器示例</span>\n        <span\n          style={{\n            padding: '2px 8px',\n            background: '#e6f7ff',\n            borderRadius: '4px',\n            fontSize: '12px',\n            color: '#1890ff',\n          }}\n        >\n          JavaScript\n        </span>\n      </Space>\n      <Space>\n        <Button size=\"small\" type=\"text\">\n          运行\n        </Button>\n        <Button size=\"small\" type=\"text\">\n          分享\n        </Button>\n      </Space>\n    </div>\n  );\n\n  return (\n    <CodeHighlighter lang=\"javascript\" header={customHeader}>\n      {code}\n    </CodeHighlighter>\n  );\n};\n\nexport default App;\n";;
  