  export default "import { OpenAIFilled } from '@ant-design/icons';\nimport { Sender, Suggestion } from '@ant-design/x';\nimport { type GetProp, message } from 'antd';\nimport React, { useState } from 'react';\n\ntype SuggestionItems = Exclude<GetProp<typeof Suggestion, 'items'>, () => void>;\n\nconst suggestions: SuggestionItems = [\n  { label: 'Write a report', value: 'report' },\n  { label: 'Draw a picture', value: 'draw' },\n  {\n    label: 'Check some knowledge',\n    value: 'knowledge',\n    icon: <OpenAIFilled />,\n    children: [\n      {\n        label: 'About React',\n        value: 'react',\n      },\n      {\n        label: 'About Ant Design',\n        value: 'antd',\n      },\n    ],\n  },\n];\n\nconst Demo: React.FC = () => {\n  const [value, setValue] = useState('');\n  const [loading, setLoading] = useState(false);\n  const [messageApi, contextHolder] = message.useMessage();\n\n  return (\n    <Suggestion\n      items={suggestions}\n      onSelect={(itemVal) => {\n        setValue(`[${itemVal}]:`);\n      }}\n    >\n      {({ onTrigger, onKeyDown, open }) => {\n        console.log(open, 'suggestion open');\n        return (\n          <>\n            {contextHolder}\n            <Sender\n              loading={loading}\n              value={value}\n              onSubmit={(value) => {\n                messageApi.success(`message send success: ${value}`);\n                setValue('');\n                setLoading(true);\n                setTimeout(() => {\n                  setLoading(false);\n                }, 3000);\n              }}\n              onChange={(nextVal) => {\n                if (nextVal === '/') {\n                  onTrigger();\n                } else if (!nextVal) {\n                  onTrigger(false);\n                }\n                setValue(nextVal);\n              }}\n              onKeyDown={onKeyDown}\n              placeholder=\"输入 / 获取建议\"\n            />\n          </>\n        );\n      }}\n    </Suggestion>\n  );\n};\n\nexport default Demo;\n";;
  