
  import '/home/runner/work/x/x/packages/x/components/suggestion/demo/trigger.tsx?watch=parent';
  export default "import { Suggestion } from '@ant-design/x';\nimport { Select } from 'antd';\nimport React from 'react';\n\nlet _uuid = 0;\n\nconst Demo: React.FC = () => {\n  const [tags, setTags] = React.useState<string[]>([]);\n  const [value, setValue] = React.useState('');\n\n  return (\n    <Suggestion<string>\n      items={(info) => [{ label: `Trigger by '${info}'`, value: String(info) }]}\n      onSelect={() => {\n        _uuid += 1;\n        setTags([...tags, `Cell_${value.replace(/\\//g, '')}`]);\n        setValue('');\n      }}\n    >\n      {({ onTrigger, onKeyDown }) => {\n        return (\n          <Select\n            value={tags}\n            style={{ width: '100%' }}\n            mode=\"tags\"\n            open={false}\n            showSearch={{\n              searchValue: value,\n              onSearch: (nextVal) => {\n                setValue(nextVal);\n              },\n            }}\n            onChange={(nextTags) => {\n              setTags(nextTags);\n            }}\n            onKeyDown={(e) => {\n              if (e.key === '/' || e.key === '#') {\n                onTrigger(e.key);\n              }\n              onKeyDown(e);\n            }}\n            placeholder=\"可任意输入 / 与 # 多次获取建议\"\n          />\n        );\n      }}\n    </Suggestion>\n  );\n};\n\nexport default Demo;\n";;
  