  export default "import { AntDesignOutlined, CopyOutlined, RedoOutlined, UserOutlined } from '@ant-design/icons';\nimport type { BubbleItemType, BubbleListProps } from '@ant-design/x';\nimport { Actions, Bubble } from '@ant-design/x';\nimport type { GetRef } from 'antd';\nimport { Avatar, Button, Flex } from 'antd';\nimport React, { useCallback, useEffect } from 'react';\n\nconst actionItems = [\n  {\n    key: 'retry',\n    icon: <RedoOutlined />,\n    label: 'Retry',\n  },\n  {\n    key: 'copy',\n    icon: <CopyOutlined />,\n    label: 'Copy',\n  },\n];\n\nlet id = 0;\n\nconst getKey = () => `bubble_${id++}`;\n\nconst genItem = (isAI: boolean, config?: Partial<BubbleItemType>, repeat = 50) => {\n  return {\n    key: getKey(),\n    role: isAI ? 'ai' : 'user',\n    content: `${id} : ${isAI ? 'Mock AI content'.repeat(repeat) : 'Mock user content.'}`,\n    ...config,\n  };\n};\n\nfunction useBubbleList(initialItems: BubbleItemType[] = []) {\n  const [items, setItems] = React.useState<BubbleItemType[]>(initialItems);\n\n  const appendItem = useCallback((item: BubbleItemType) => {\n    setItems((prev) => [...prev, item]);\n  }, []);\n\n  return [items, setItems, appendItem] as const;\n}\n\nconst App = () => {\n  const listRef = React.useRef<GetRef<typeof Bubble.List>>(null);\n  const [items, setItems, add] = useBubbleList();\n\n  useEffect(() => {\n    setItems([\n      genItem(false, { typing: false }),\n      genItem(true, { typing: false }),\n      genItem(false, { typing: false }),\n      genItem(true, { typing: false }),\n      genItem(false, { typing: false }),\n      genItem(true, { typing: false }),\n      genItem(false, { typing: false }),\n      genItem(true, { typing: false }),\n      genItem(false, { typing: false }),\n      genItem(true, { typing: false }),\n      genItem(false, { typing: false }),\n    ]);\n  }, []);\n\n  const memoRole: BubbleListProps['role'] = React.useMemo(\n    () => ({\n      ai: {\n        typing: true,\n        header: 'AI',\n        avatar: () => <Avatar icon={<AntDesignOutlined />} />,\n        footer: (content) => <Actions items={actionItems} onClick={() => console.log(content)} />,\n      },\n      user: {\n        placement: 'end',\n        typing: false,\n        header: 'User',\n        avatar: () => <Avatar icon={<UserOutlined />} />,\n      },\n    }),\n    [],\n  );\n\n  return (\n    <Flex vertical style={{ height: 720 }} gap=\"small\">\n      <Flex gap=\"small\" wrap>\n        <Button\n          type=\"primary\"\n          onClick={() => {\n            const isAI = !!(items.length % 2);\n            add(genItem(isAI, { typing: { effect: 'fade-in', step: [20, 50] } }, 500));\n          }}\n        >\n          Add Long Bubble\n        </Button>\n        <Button onClick={() => listRef.current?.scrollTo({ top: 'top' })}>Scroll To Top</Button>\n        <Button onClick={() => listRef.current?.scrollTo({ top: 'bottom', behavior: 'smooth' })}>\n          Scroll To Bottom smooth\n        </Button>\n        <Button onClick={() => listRef.current?.scrollTo({ top: 'bottom', behavior: 'instant' })}>\n          Scroll To Bottom instant\n        </Button>\n        <Button\n          onClick={() =>\n            listRef.current?.scrollTo({\n              top: Math.random() * listRef.current.scrollBoxNativeElement.scrollHeight,\n            })\n          }\n        >\n          Scroll To Random\n        </Button>\n        <Button onClick={() => listRef.current?.scrollTo({ key: items[1].key, block: 'nearest' })}>\n          Scroll To Second Bubble\n        </Button>\n        <Button\n          onClick={() =>\n            listRef.current?.scrollTo({ key: items[items.length - 1].key, block: 'end' })\n          }\n        >\n          Scroll To Last Bubble\n        </Button>\n      </Flex>\n\n      <div style={{ display: 'flex', flex: 1, minHeight: 0 }}>\n        <Bubble.List\n          ref={listRef}\n          role={memoRole}\n          items={items}\n          onScroll={(e) => {\n            console.log('scroll', (e.target as HTMLDivElement).scrollTop);\n          }}\n        />\n      </div>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  