  export default "import { CloudUploadOutlined, PaperClipOutlined } from '@ant-design/icons';\nimport type { AttachmentsProps } from '@ant-design/x';\nimport { Attachments, Sender } from '@ant-design/x';\nimport type { GetProp, GetRef } from 'antd';\nimport { Badge, Button, Flex, Tooltip } from 'antd';\nimport React, { useState } from 'react';\n\nconst App: React.FC = () => {\n  const [loading, setLoading] = useState(false);\n  const [open, setOpen] = useState(false);\n  const [items, setItems] = React.useState<GetProp<AttachmentsProps, 'items'>>([]);\n  const senderRef = React.useRef<GetRef<typeof Sender>>(null);\n  const [value, setValue] = useState('');\n\n  const submitDisabled = items.length === 0 && !value && !loading;\n\n  const senderHeader = (\n    <Sender.Header\n      title=\"Attachments\"\n      open={open}\n      onOpenChange={setOpen}\n      styles={{\n        content: {\n          padding: 0,\n        },\n      }}\n    >\n      <Attachments\n        // Mock not real upload file\n        beforeUpload={() => false}\n        items={items}\n        onChange={({ file, fileList }) => {\n          const updatedFileList = fileList.map((item) => {\n            if (item.uid === file.uid && file.status !== 'removed' && item.originFileObj) {\n              // clear URL\n              if (item.url?.startsWith('blob:')) {\n                URL.revokeObjectURL(item.url);\n              }\n              // create new preview URL\n              return {\n                ...item,\n                url: URL.createObjectURL(item.originFileObj),\n              };\n            }\n            return item;\n          });\n          setItems(updatedFileList);\n        }}\n        placeholder={(type) =>\n          type === 'drop'\n            ? {\n                title: 'Drop file here',\n              }\n            : {\n                icon: <CloudUploadOutlined />,\n                title: 'Upload files',\n                description: 'Click or drag files to this area to upload',\n              }\n        }\n        getDropContainer={() => senderRef.current?.nativeElement}\n      />\n    </Sender.Header>\n  );\n\n  return (\n    <Flex style={{ height: 350 }} align=\"end\">\n      <Sender\n        submitType=\"enter\"\n        ref={senderRef}\n        header={senderHeader}\n        value={value}\n        onChange={setValue}\n        placeholder=\"Enter to send message\"\n        prefix={\n          <Badge dot={items.length > 0 && !open}>\n            <Button\n              type=\"text\"\n              onClick={() => setOpen(!open)}\n              icon={<PaperClipOutlined style={{ fontSize: 16 }} />}\n            />\n          </Badge>\n        }\n        suffix={(_, info) => {\n          const { SendButton, LoadingButton } = info.components;\n          if (loading) {\n            return (\n              <Tooltip title=\"Click to cancel\">\n                <LoadingButton />\n              </Tooltip>\n            );\n          }\n\n          const node = <SendButton disabled={submitDisabled} />;\n          return node;\n        }}\n        onSubmit={() => {\n          setValue('');\n          setItems([]);\n          setLoading(true);\n          setTimeout(() => {\n            setLoading(false);\n          }, 1000);\n        }}\n      />\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  