  export default "import { OpenAIFilled } from '@ant-design/icons';\nimport { Sender, Suggestion } from '@ant-design/x';\nimport { Flex, GetProp } from 'antd';\nimport React from 'react';\nimport SemanticPreview from '../../../.dumi/components/SemanticPreview';\nimport useLocale from '../../../.dumi/hooks/useLocale';\n\nconst locales = {\n  cn: {\n    root: '根节点',\n    content: '标题容器',\n    popup: '列表容器',\n  },\n  en: {\n    root: 'Root',\n    content: 'Content',\n    popup: 'Popup',\n  },\n};\n\ntype SuggestionItems = Exclude<GetProp<typeof Suggestion, 'items'>, () => void>;\n\nconst suggestions: SuggestionItems = [\n  { label: 'Write a report', value: 'report' },\n  { label: 'Draw a picture', value: 'draw' },\n  {\n    label: 'Check some knowledge',\n    value: 'knowledge',\n    icon: <OpenAIFilled />,\n    children: [\n      {\n        label: 'About React',\n        value: 'react',\n      },\n      {\n        label: 'About Ant Design',\n        value: 'antd',\n      },\n    ],\n  },\n];\n\nconst App: React.FC = () => {\n  const [locale] = useLocale(locales);\n\n  return (\n    <Flex vertical>\n      <SemanticPreview\n        height={300}\n        componentName=\"Suggestion\"\n        semantics={[\n          { name: 'root', desc: locale.root },\n          { name: 'content', desc: locale.content },\n          { name: 'popup', desc: locale.popup },\n        ]}\n      >\n        <Suggestion\n          open={true}\n          getPopupContainer={(triggerNode) => triggerNode.parentElement!}\n          items={suggestions}\n        >\n          {({ onKeyDown }) => {\n            return <Sender onKeyDown={onKeyDown} placeholder=\"输入 / 获取建议\" />;\n          }}\n        </Suggestion>\n      </SemanticPreview>\n    </Flex>\n  );\n};\n\nexport default App;\n";;
  