  export default "import { Sender, Suggestion, SuggestionProps } from '@ant-design/x';\nimport { message, Tag } from 'antd';\nimport React, { useState } from 'react';\n\ntype SuggestionItems = Exclude<SuggestionProps['items'], () => void>;\n\nconst suggestions: SuggestionItems = [\n  { label: 'Write a report', value: 'report' },\n  { label: 'Draw a picture', value: 'draw' },\n  {\n    label: 'Check some knowledge',\n    value: 'knowledge',\n    extra: <Tag>Extra Info</Tag>,\n    other: 'other data',\n  },\n];\n\nconst Demo: React.FC = () => {\n  const [value, setValue] = useState('');\n  const [loading, setLoading] = useState(false);\n  const [messageApi, contextHolder] = message.useMessage();\n  return (\n    <Suggestion\n      items={suggestions}\n      onSelect={(itemVal) => {\n        setValue(`[${itemVal}]:`);\n      }}\n      block\n    >\n      {({ onTrigger, onKeyDown }) => {\n        return (\n          <>\n            {contextHolder}\n            <Sender\n              loading={loading}\n              value={value}\n              onSubmit={(value) => {\n                messageApi.success(`message send success: ${value}`);\n                setValue('');\n                setLoading(true);\n                setTimeout(() => {\n                  setLoading(false);\n                }, 3000);\n              }}\n              onChange={(nextVal) => {\n                if (nextVal === '/') {\n                  onTrigger();\n                } else if (!nextVal) {\n                  onTrigger(false);\n                }\n                setValue(nextVal);\n              }}\n              onKeyDown={onKeyDown}\n              placeholder=\"输入 / 获取建议\"\n            />\n          </>\n        );\n      }}\n    </Suggestion>\n  );\n};\n\nexport default Demo;\n";;
  